import axios from 'axios'
const baseURL = '/players'

const getPlayersServices = () => {
    const request = axios.get(baseURL)
    return request.then(response => response.data)
}

const deletePlayerServices = id => {
    const request = axios.delete(`${baseURL}/${id}`)
    return request.then(response => response.data)
}

const createPlayerServices = obj => {
    const request = axios.post(baseURL, obj)
    return request.then(response => response.data)
}

const editPlayerServices = (id, obj) => {
    const request = axios.put(`${baseURL}/${id}`, obj)
    return request.then(response => response.data)
}

const toggleServices = (id, obj) => {
    const request = axios.patch(`${baseURL}/${id}`, obj)
    return request.then(response => response.data)
}

export default {getPlayersServices, deletePlayerServices, createPlayerServices, editPlayerServices, toggleServices}