import React, { useState, useEffect } from 'react'
import AddPlayer from './components/AddPlayer'
import DisplayPlayers from './components/DisplayPlayers'
import EditPlayer from './components/EditPlayer'
import Service from './services/players'

const App = () => {
  const [editing, setEditing] = useState(false)
  const [playerToEdit, setPlayerToEdit] = useState()
  const [players, setPlayers] = useState([])

  const addPlayer = obj => {
    const newObj = {
      name: obj.name,
      club: obj.club,
      important: false
    }

    Service
      .createPlayerServices(newObj)
      .then(data => {
        setPlayers(players.concat(data))
      })
  }

  const deletePlayer = id => {
    Service
      .deletePlayerServices(id)
      .then(data => {
        setPlayers(players.filter(p => p.id !== id))
      })
  }

  const whichPlayer = obj => {
    setEditing(true);
    setPlayerToEdit(obj);
  };

  const editPlayer = (id, obj) => {
    Service
      .editPlayerServices(id, obj)
      .then(data => {
        setPlayers(players.map(p => p.id === id ? data : p))       
        setEditing(false)
      })
  }

  const toggle = (id, obj) => {
    Service
      .toggleServices(id, obj)
      .then(data => {
        setPlayers(players.map(p => p.id === id ? data : p))
      })
  }

  useEffect(() => {
    Service
      .getPlayersServices()
      .then(data => {
        setPlayers(data)
      })
  }, [])

  return (
    <div className="center">
      <h1>Practicing</h1>
      <hr />

      {editing ?
        <EditPlayer
          setEditing={setEditing}
          playerToEdit={playerToEdit}
          editPlayer={editPlayer}
        /> :
        <AddPlayer
          addPlayer={addPlayer}
        />}

      <DisplayPlayers
        players={players}
        deletePlayer={deletePlayer}
        whichPlayer={whichPlayer}
        toggle={toggle}
      />
    </div>
  )
}

export default App
